
.container-fluid {
  max-width: 1305px;
}

/* Large devices */
/* Medium devices */
/* Small devices */
/* Extra small devices */
a:hover {
  text-decoration: none !important;
}
#app {
  font-family: 'Roboto', sans-serif;
}

/* Default responsive typography */
html {
  font-size: 32px;
}
@media (max-width: 1199px) {
html {
    font-size: 28px;
}
}
@media (max-width: 991px) {
html {
    font-size: 20px;
}
}
@media (max-width: 767px) {
html {
    font-size: 16px;
}
}
@media (max-width: 575px) {
html {
    font-size: 14px;
}
}



